import React from 'react';
import Media from 'react-media';
import style from './second-screen.module.css';
import imgPortal from '../../img/infoImg/portal.png';
import Header from './header/header';
import Information from './Imformation/information';
import NavigationServices from '../navigationServices/navigationServices';

const SectionTwo = () => (
  <section id="SecondContainer" className={style.section}>
    <Header />
    <Media query={{ minWidth: 1200 }}>
      {matches =>
        matches && (
          <div className={style.navigation}>
            <NavigationServices number={1} />
          </div>
        )
      }
    </Media>
    <Information />
    <Media query={{ minWidth: 1200 }}>
      {matches =>
        matches && <img className={style.img} alt="Portal" src={imgPortal} />
      }
    </Media>
  </section>
);

export default SectionTwo;
