import React from 'react';
import classNames from 'classnames';
import style from './navigationServices.module.css';

const arr = [
  { name: '01', href: '#SecondContainer' },
  { name: '02', href: '#ThirdContainer' },
  { name: '03', href: '#' },
  { name: '04', href: '#' },
  { name: '05', href: '#' },
  { name: '06', href: '#' },
  { name: '07', href: '#' },
];
const linkStyle = classNames(style.listItem, style.active);

const Navigation = ({ number }) => (
  <div className={style.wrapper}>
    <ul className={style.list}>
      {arr.map((el, i) =>
        number === i + 1 ? (
          <li className={linkStyle} key={el.name}>
            <a className={style.link} href={el.href}>
              {el.name}
            </a>
          </li>
        ) : (
          <li className={style.listItem} key={el.name}>
            <a className={style.link} href={el.href}>
              {el.name}
            </a>
          </li>
        ),
      )}
    </ul>
  </div>
);

export default Navigation;
